<template>
  <div ref="app" id="app">
    <el-card class="card" shadow="always">
      <el-row type="flex" justify="center">
        <el-col>
          <el-steps :active="active" finish-status="success" align-center>
            <el-step title="上传图片"></el-step>
            <el-step title="选择模版"></el-step>
            <el-step title="生成表情包"></el-step>
          </el-steps>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center">
        <el-col>
          <div ref="main" style="margin-top: 20px">
            <photograph
              v-if="active == 1"
              v-model="headImage"
              @upload="upload"
            ></photograph>
            <el-row :gutter="20" v-if="active == 2" type="flex" justify="center">
              <el-col :span="9">
                <el-card>
                  <div slot="header">
                    <span>选择模版</span>
                  </div>
                  <div>
                    <el-table
                  ref="faceTable"
                  :show-header="false"
                  highlight-current-row
                  :data="tableData"
                  :height="250"
                  :max-height="250"
                  style="width: 260px"
                  @row-click="handleSelect"
                >
                  <el-table-column width="2px" />
                  <el-table-column>
                    <template slot-scope="scope">
                      <el-card>
                        <el-image
                          :src="scope.row.img"
                          style="width: 200px; height: 200px"
                        ></el-image>
                      </el-card>
                    </template>
                  </el-table-column>
                </el-table>
                  </div>
                </el-card>
              </el-col>
              <el-col :span="9">
                <el-card>
                  <div slot="header">
                    <span>模版描述</span>
                  </div>
                  <div style="height:250px">
                    <span style="margin-top:20%">{{description}}</span>
                  </div>
                </el-card>
              </el-col>
            </el-row>
            <el-row v-if="active == 3" type="flex" justify="center">
              <el-col :span="12">
                <el-image
              v-loading="loading"
              element-loading-text="表情包生成中"
              element-loading-spinner="el-icon-loading"
              :src="face"
              :fit="fit"
              style="width: 400px; height: 400px"
            ></el-image>
              </el-col>
            </el-row>
          </div>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" style="margin-top:10px">
        <el-col :span="3" v-if="active != 3">
          <el-button :disabled="next" type="primary" @click="nextStep"
            >下一步</el-button
          >
        </el-col>
        <el-col :span="3" v-if="active == 2">
          <el-button @click="preStep">上一步</el-button>
        </el-col>
        <el-col v-if="active == 3" :span="6">
          <el-button type="primary" @click="save">保存</el-button>
          <el-button
            type="danger"
            @click="
              () => {
                this.active = 1;
              }
            "
            >重新开始</el-button
          >
        </el-col>
        <!-- <el-col :span="4">
          
        </el-col> -->
      </el-row>
    </el-card>
    <!-- <el-row>
      <el-col>
        
      </el-col>
    </el-row> -->
  </div>
</template>

<script>
import Photograph from "./components/photograph.vue";
import noview from "@/assets/images/noView.jpg";
import { imgsnum,des } from "@/format.js";
import { getStick } from "@/api/axios/msg.js";
import CanvasNest from "canvas-nest.js";

export default {
  name: "app",
  components: {
    Photograph,
  },
  data() {
    return {
      headImage: "",
      disabled: true,
      next: true,
      tableData: [],
      select: null,
      face: null,
      noView: noview,
      active: 1,
      loading: true,
      description:''
    };
  },
  methods: {
    upload() {
      this.next = false;
    },
    handleSelect(row) {
      this.$refs.faceTable.setCurrentRow(row);
      this.disabled = false;
      this.next = false;
      this.select = row.imgIndex;
      this.description=des[row.imgIndex]
      console.log(row.imgIndex);
    },
    Init() {
      const config = {
        color: "0,0,255",
        count: 200,
      };
      // Using config rendering effect at 'element'.
      const cn = new CanvasNest(this.$refs["app"], config);
      for (let i = 1; i <= imgsnum; i++) {
        let adr = "@/assets/images/" + i.toString() + ".jpg";
        console.log(adr);
        this.tableData.push({
          imgIndex: i,
          img: require("@/assets/images/" + i.toString() + ".jpg"),
        });
      }
    },
    handleFace() {
      //window.alert(this.select);
      this.loading = true;
      let data = new FormData();
      data.append("name", this.select);
      data.append("image", this.headImage);
      getStick(data).then((res) => {
        this.loading = false;
        this.face = window.URL.createObjectURL(res.data);
      });
    },
    nextStep() {
      this.active++;
      this.next = true;
      if (this.active == 3) {
        this.handleFace();
      }
    },
    preStep() {
      this.active--;
    },
  },
  mounted() {
    this.Init();
  },
};
</script>

